* {
  margin: 0;
  padding: 0;
  font-family: "Source Serif Pro", serif;
}

#header-component {
  height: 100px;
  background-color: #363c3c;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 100;
}

.logo {
  padding: 0 25px 0 25px;
}

.logo img {
  height: 75px;
}

.search {
  width: 25%;
  height: 65%;
  padding-left: 5px;
  margin: 0 35px 0 auto;
  outline: none;
  border: none;
  border-radius: 10px;
  box-sizing: border-box;
  font-size: 24px;
  -webkit-transition: box-shadow 0.6s linear;
  transition: box-shadow 0.6s linear;
}

.search::-webkit-input-placeholder {
  padding-left: 10px;
  color: #878b8b;
}

.search::-moz-placeholder {
  padding-left: 10px;
  color: #878b8b;
}

.search:-ms-input-placeholder {
  padding-left: 10px;
  color: #878b8b;
}

.search::-ms-input-placeholder {
  padding-left: 10px;
  color: #878b8b;
}

.search::placeholder {
  padding-left: 10px;
  color: #878b8b;
}

.search:focus {
  box-shadow: 0px 0px 0px 5px #878b8b;
}

@media only screen and (max-width: 1023px) {
  .search {
    font-size: 16px;
    width: 40%;
  }
}

.sidebar {
  width: 20%;
  height: 100%;
  background-color: #d6d8d8;
  position: fixed;
  top: 0;
  z-index: 1;
}

#sidebar-logo {
  display: flex;
}

#sidebar-logo > a {
  margin: auto;
}

#sidebar-logo img {
  margin-top: 10px;
  height: 90px;
}

.random-button {
  margin: 50px auto 30px auto;
  width: 85%;
  background-color: white;
  border-radius: 10px;
  border: 1px solid black;
}

.random {
  width: 100%;
  height: 45px;
  font-size: 22px;
  font-weight: bold;
  border: none;
  border-radius: 10px;
  background-color: white;
  cursor: pointer;
}

.flex {
  display: flex;
  flex-direction: column;
}

.colors {
  background: none;
  border: none;
  outline: none;
  color: black;
  text-decoration: none;
  font-size: 24px;
  margin: 10px 0 10px 10%;
  cursor: pointer;
}

.random:focus,
.clear-button > button:focus {
  outline: none;
}

.colors:hover {
  color: lightgreen;
  text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
}

.random:hover,
.clear-button > button:hover {
  text-decoration: underline;
  background: red;
  background: -webkit-linear-gradient(
    left,
    orange,
    yellow,
    green,
    cyan,
    blue,
    purple
  );
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(orange),
    color-stop(yellow),
    color-stop(green),
    color-stop(cyan),
    color-stop(blue),
    to(purple)
  );
  background: linear-gradient(
    to right,
    orange,
    yellow,
    green,
    cyan,
    blue,
    purple
  );
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: rgba(0, 0, 0, 0.3);
}

.colors:focus,
.random:focus,
.clear-button > button:focus {
  color: red;
}

@media only screen and (max-width: 1023px) {
  .random {
    font-size: 12px;
  }
  .colors {
    font-size: 16px;
  }
}

@media only screen and (max-width: 426px) {
  #sidebar-logo img {
    height: 50px;
  }
}

.card {
  width: calc(80vw / 5);
  height: 200px;
  margin: 1em 1em 0 1em;
  border-radius: 15px;
  box-shadow: 0 0 0 3px #e3e3e3;
}

.card:hover {
  margin: 0.5em 1em 0 0.5em;
  box-shadow: 0 0 20px lightgreen;
  -webkit-transition: margin 300ms ease-in;
  transition: margin 300ms ease-in;
}

.card-color {
  width: 100%;
  height: 80%;
  border-radius: 15px 15px 0 0;
  cursor: pointer;
}

.card-text {
  margin-left: 1.5em;
  height: 20%;
  display: table;
}

.card-text > p {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
  font-size: 22px;
}

@media only screen and (max-width: 1023px) {
  .card {
    width: calc(100vw / 3);
    margin: 5px;
  }
  .card:hover {
    margin-bottom: 0;
    box-shadow: 0 0 20px lightgreen;
    -webkit-transition: margin-bottom 300ms ease-in;
    transition: margin-bottom 300ms ease-in;
  }
}

#list-section {
  margin-left: 20%;
  padding-top: 50px;
  display: grid;
  grid-template-columns: auto auto auto auto;
  justify-content: center;
}

@media only screen and (max-width: 1023px) {
  #list-section {
    grid-template-columns: auto auto;
  }
}

.pages {
  width: 80%;
  margin: 1em 0 30px 20%;
  text-align: center;
}

.page-link {
  padding: 8px 16px;
  font-size: 20px;
  line-height: 1.5;
  text-decoration: none;
}

.page-link:hover {
  background-color: #c0c0c0;
}

.page-link:focus {
  color: red;
}

#detail {
  margin-left: 20%;
  padding-top: 50px;
}

#detail > .card {
  width: 95%;
  height: 70vh;
  margin: auto;
  border: 1px solid black;
}

#detail > .card p {
  font-size: 32px;
  font-weight: bolder;
}

.flex-detail {
  padding-top: 25px;
  display: grid;
  grid-template-columns: auto auto auto auto auto;
  justify-content: center;
}

.flex-detail > .card {
  width: calc(80vw / 6);
  margin: 0 10px 15px 10px;
  height: 150px;
}

.clear-button {
  margin: 10px 0 15px 0;
  text-align: center;
}

.clear-button > button {
  width: 200px;
  height: 50px;
  font-size: 22px;
  font-weight: bold;
  background-color: white;
  border-radius: 10px;
  border: 1px solid black;
  cursor: pointer;
}

@media only screen and (max-width: 1023px) {
  .flex-detail {
    grid-template-columns: auto auto;
  }
  .flex-detail > .card {
    width: calc(80vw / 3);
    height: 100px;
  }
}

