.card {
  width: calc(80vw / 5);
  height: 200px;
  margin: 1em 1em 0 1em;
  border-radius: 15px;
  box-shadow: 0 0 0 3px #e3e3e3;
}

.card:hover {
  margin: 0.5em 1em 0 0.5em;
  box-shadow: 0 0 20px lightgreen;
  transition: margin 300ms ease-in;
}

.card-color {
  width: 100%;
  height: 80%;
  border-radius: 15px 15px 0 0;
  cursor: pointer;
}

.card-text {
  margin-left: 1.5em;
  height: 20%;
  display: table;
}

.card-text > p {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
  font-size: 22px;
}

@media only screen and (max-width: 1023px) {
  .card {
    width: calc(100vw / 3);
    margin: 5px;
  }
  .card:hover {
    margin-bottom: 0;
    box-shadow: 0 0 20px lightgreen;
    transition: margin-bottom 300ms ease-in;
  }
}
